.nav-tabs {
  float: left;
  width: 100%;
  margin: 0;
  list-style-type: none;
  border-bottom: 1px solid transparent;

  > li {
    float: left;
    margin-bottom: -1px;
    > a {
      margin-right: 2px;
      line-height: 1.5;
      padding: 10px;
      border: 1px solid transparent;
      border-radius: 4px 4px 0 0;
      float: left;
      text-decoration: none;

      &:hover {
        border-color: #eee #eee #ddd;
      }
    }
    &.active > a {
      &,
      &:hover,
      &:focus {
        color: #555;
        cursor: default;
        background-color: #fff;
        border-color: transparent;
      }
    }
  }
}
.tab-content{
  width: 98%;
  display: inline-block;
  > .tab-pane {
    float: left;
    display: none;
    width: 100%;
    &.active {
      display: block;
      padding-left: 25px;
      padding-bottom: 25px;
      background-color: #fff;
      box-shadow: 0 5px 4px -2px rgba(0, 0, 0, 0.15);
    }
  }
}
